import "./styles.css";
import axios from "axios";
import { useState, useEffect } from "react";

var indexvalue = 0;

function AddList() {
  return (
    <div className="addlist">
      <span>Current weather</span>
      <span className="add">Add a new city data</span>
    </div>
  );
}

function Form(props) {
  return (
    <form className="form">
      Name: <input type="text" name="name" id="name" />
      <br />
      Age: <input type="text" name="age" id="age" />
      <br />
      Course: <input type="text" name="course" id="course" />
      <br />
      Department: <input type="text" name="dep" id="dep" />
      <br />
      <button type="submit" onClick={props.submit}>
        Submit
      </button>
    </form>
  );
}

var data = [
  { id: 0, name: "Manoj", age: 20, course: "Mern", dep: "Mech" },
  { id: 1, name: "Chaitanya", age: 20, course: "Mern", dep: "Mech" },
  { id: 2, name: "Arun", age: 20, course: "Mern", dep: "Mech" },
  { id: 3, name: "Balaji", age: 20, course: "Mern", dep: "Mech" },
  { id: 4, name: "Sanjay", age: 20, course: "Mern", dep: "Mech" },
  { id: 5, name: "Krishna", age: 20, course: "Mern", dep: "Mech" }
];

function Weather() {
  const [t, f] = useState(false);

  const [list, setlist] = useState(data);

  function Onclick(value) {
    f(!t);

    indexvalue = value;
    console.log(indexvalue);
  }

  function Tbody(props) {
    return (
      <tr className="TableRow">
        <td className="PerformanceCell">{props.name}</td>
        <td className="PerformanceCell">{props.age}</td>
        <td className="PerformanceCell">{props.course}</td>
        <td className="PerformanceCell">{props.dep}</td>
        <td className="PerformanceCell" onClick={Onclick}>
          edit
        </td>
      </tr>
    );
  }

  function Tbodyproto(hel) {
    return (
      <Tbody
        key={hel.id}
        name={hel.name}
        course={hel.course}
        age={hel.age}
        dep={hel.dep}
      />
    );
  }

  function Table(props) {
    return (
      <table className="PerformanceTable">
        <thead>
          <tr>
            <th className="TableHeader">Name</th>
            <th className="TableHeader">Age</th>
            <th className="TableHeader">Course</th>
            <th className="TableHeader">Department</th>
            <th className="TableHeader">Edit</th>
          </tr>
        </thead>
        <tbody>{list.map(Tbodyproto)}</tbody>
      </table>
    );
  }

  return (
    <div>
      {t ? null : <AddList />}
      {t ? null : <Table />}
      {t ? <Form submit={Onclick} /> : null}
    </div>
  );
}

export default Weather;
